<template>
  <div>
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus tramites
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <div class="mb-2" style="color: #084068">{{ operation.document_name }}</div>

          <div class="call-to-action">
            <v-row>
              <v-col class="text-left">
                <div style="background-color: #184C72; color: white; font-weight: bold">Servicio:
                  {{ operation.document_name }}
                </div>
                <div>Método de pago: Deposito bancario</div>
                <div>Departamento: {{ operation.ubigeo_department.name }}</div>
                <div>Especialidad: {{ operation.lawyer_specialty.name }}</div>
                <div>Código de pedido: {{ operation.id | padleft }}</div>
                <div>Precio pagado: S/{{ operation.price }}</div>
                <div v-for="extra in operationFormData" :key="extra.id">
                  {{ extra.name }} : {{ extra.value }}
                </div>
                <div v-if="operation.hasDocument">
                  Documento:
                  <a target="_blank" :href="operation.documentUrl">{{ operation.documentUrl }}</a>
                </div>
              </v-col>
            </v-row>
          </div>

          <div class="call-to-action">
            <v-row v-for="log in logs" :key="log.id">
              <v-col class="text-left">
                <div style="background-color: #184C72; color: white; font-weight: bold">Estado: {{
                    log.status.name
                  }}
                </div>
                <div>Fecha: {{ date(log.created_at).format('DD/MM/YYYY') }}</div>
                <div>Hora: {{ date(log.created_at).format('hh:mm A') }}</div>
                <div>Comentario: {{ log.comment }}</div>
              </v-col>
            </v-row>
          </div>
        </v-col>
      </v-row>

      <v-row>
        <v-col class="text-center">
          <v-btn v-if="operation.hasDocument" @click="goToDocument"
                 :href="operation.documentUrl" target="_blank"
                 color="secondary" block>
            Documento final
          </v-btn>
        </v-col>
      </v-row>

      <button-back @click="goToIndex"></button-back>

    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"
import ButtonBack from "../../components/ButtonBack";
import moment from "moment";

export default {
  name: 'OperationInvoice',
  components: {ButtonBack},
  data: () => ({
    operation: {status: '', ubigeo_department: '', lawyer_specialty: ''},
    operationFormData: [],
    logs: [],
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    }
  },
  mounted() {
    this.loadOperation()
    this.loadLogs()
  },
  methods: {
    goToIndex() {
      this.$router.push({name: 'OperationIndex'})
    },
    loadOperation() {
      let url = 'operations/' + this.operationId
      axios.get(url).then((response) => {
        this.operation = response.data
        this.loadFormData()
      })
    },
    loadFormData() {
      let url = 'external-resources-forms/' + this.operation.resource_id + '?operation_id=' + this.operation.id
      axios.get(url).then((response) => {
        this.operationFormData = response.data
      }).catch().then(() => {

      })
    },
    loadLogs() {
      axios.get('/logs?model_id=' + this.operationId + '&model_type=App\\Operation').then((response) => {
        this.logs = response.data;
      });
    },
    date(date) {
      return moment(date);
    },
  }
}
</script>
